import PropTypes from "prop-types";

import styleModule from "./Sharing.module.css"
import SharingButton from "./ShareButton"


const defaultItems = ["twitter", "linkedin", "facebook", "email"];

const SharingSection = ({title, items=defaultItems, className="", style={}}) => (
    <div className={`${styleModule.sharing} ${className}`} style={style}>
        <div className={styleModule.sharing__title}>{title}</div>
        {items.map((type, index) => <SharingButton key={index} type={type}/>)}
    </div>
);

SharingSection.propTypes = {
    title: PropTypes.string,
    items: PropTypes.array,
    className: PropTypes.string,
    style: PropTypes.object
}


export default SharingSection;