import PropTypes from 'prop-types';
import cx from 'classnames';
import styles from "./styles.module.css";


const Avatar = ({ src, alt, size, onClick }) => {
    return (
        <div
            className={cx(
                styles.Avatar,
                onClick ? styles.hasListener : null,
            )}
        >
            <img
                src={src}
                alt={alt}
                onClick={onClick}
                style={{
                    width: size,
                    height: size,
                }}
            />
        </div>
    );
};

Avatar.defaultProps = {
    onClick: null,
};

Avatar.propTypes = {
    onClick: PropTypes.func,
};

export default Avatar;
