import React from 'react';
import PropTypes from "prop-types";
import cx from "classnames"

import styles from "./IconText.module.css"


const IconText = ({icon, text, size="default", color="default"}) => (
    <div className={cx(styles.iconText, styles[`color_${color}`], styles[`size_${size}`])}>
        <span className={styles.iconText__icon}>{icon ? React.cloneElement(icon, { width: "100%", height: "100%" }) : null}</span>
        <span className={styles.iconText__text}>{text}</span>
    </div>
);

IconText.propTypes = {
    icon: PropTypes.element,
    text: PropTypes.string.isRequired,
    size: PropTypes.oneOf(['default', 'large']),
    color: PropTypes.oneOf(['default', 'light'])
}


export default IconText;