import {EmailIcon, FacebookIcon, LinkedinIcon, TwitterIcon} from "../icon";
import styles from "./ShareButton.module.css"


const url = "https://openui5.org/ui5con/germany2020/?v3";
const oShareConfigurations = {
    facebook: {
        shareUrl: 'https://www.facebook.com/sharer/sharer.php',
        icon: FacebookIcon,
        params: {
            u: url
        }
    },
    linkedin: {
        shareUrl: 'https://www.linkedin.com/shareArticle',
        icon: LinkedinIcon,
        params: {
            url: url,
            mini: true
        }
    },
    twitter: {
        shareUrl: 'https://twitter.com/intent/tweet/',
        icon: TwitterIcon,
        params: {
            url: url
        }
    },
    email: {
        shareUrl: 'mailto:',
        icon: EmailIcon,
        params: {
            subject: "UI5con@SAP coming in June 2020",
            body: "Mark your calendars: UI5con@SAP 2020 is on June 19 in St. Leon-Rot, Germany  a daylong community event focused on UI5 development. https://openui5.org/ui5con/germany2020/"
        }
    }
}

const getShareUrl = (oConfig) => {
    const oParams = oConfig.params || {},
        aQueryParams = Object.keys(oParams).map(sParam => sParam + "=" + encodeURIComponent(oParams[sParam]));

    return oConfig.shareUrl + "?" + aQueryParams.join("&");
}


const SharingButton = ({type}) => {

    const openShare = () => {
        const oShareConfiguration = oShareConfigurations[type];
        if (!oShareConfiguration) {
            return;
        }

        const sShareUrl = getShareUrl(oShareConfiguration),
            popWidth = 600,
            popHeight = 480,
            left = window.innerWidth / 2 - popWidth / 2 + window.screenX,
            top = window.innerHeight / 2 - popHeight / 2 + window.screenY,
            popParams=`scrollbars=no, width=${popWidth}, height=${popHeight}, top=${top}, left=${left}`,
            newWindow = window.open(sShareUrl, '', popParams);

        if (window.focus) {
            newWindow.focus();
        }
    }


    if (type === "email") {
        return <a href={getShareUrl(oShareConfigurations.email)} className={styles.sharingButton}>{oShareConfigurations[type].icon()}</a>
    }
    return <div className={styles.sharingButton} onClick={openShare}>{oShareConfigurations[type].icon()}</div>
}

export default SharingButton;