import { useState, useEffect } from 'react';
import cx from 'classnames';
import styleModule from "./Agenda.module.css";

import Dialog from '../dialog';
import Section from "../section";
import Title from "../title";
import Avatar from "../avatar";
import ToggleButtons from "../toggleButton";
import AgendaSession from "./Session";
import SessionIcon from "./SessionIcon";

const AgendaJSON = require('../../../data/agenda.json')
    .map((s) => {
        let startTime = new Date(s.startTime.replace( /(\d{2}).(\d{2}).(\d{4})/, "$2/$1/$3"));
        let endTime = new Date(s.endTime.replace( /(\d{2}).(\d{2}).(\d{4})/, "$2/$1/$3"));
        s.startTime = startTime;
        s.endTime = endTime;
        return s;
    })
    .sort((s1, s2) => s1.startTime - s2.startTime);

const venueTimeFormatter = new Intl.DateTimeFormat(undefined, {
    hour: "2-digit",
    minute: "2-digit",
    hourCycle: "h23",
    timeZone: "Europe/Berlin"
});
const userTimeFormatter = new Intl.DateTimeFormat(undefined, {
    hour: "2-digit",
    minute: "2-digit",
    hourCycle: "h23",
});


const fnGetSession = (bUserTime, selectedDate) => {
   return AgendaJSON
        .filter((s) => {
            let relatedDate = bUserTime ? s.startTime.getDate() : s.startTime.getUTCDate();
            return relatedDate === selectedDate
        })
        .sort((s1, s2) => s1.startTime - s2.startTime)
}

const getDateButtonState = (bUserTime, selectedKey) => {
    let firstSessionTime = AgendaJSON[0].startTime
    let lastSessionTime = AgendaJSON[AgendaJSON.length - 1].startTime
    if (bUserTime && firstSessionTime.getDate() === lastSessionTime.getDate()) {
        return {
            selectedKey: firstSessionTime.getDate(),
            buttons: [{
                key: firstSessionTime.getDate(),
                title: "Day 1",
                subtitle: `JUL ${firstSessionTime.getDate()}`
            }]
        }
    } else {
        return {
            selectedKey: selectedKey !== undefined ? selectedKey : Date.now() > (bUserTime ? new Date(2020, 6, 10) : Date.UTC(2020, 6, 10, 2)) ? 10 : 9,
            buttons: [{
                key: 9,
                title: "Day 1",
                subtitle: "JUL 9"
            }, {
                key: 10,
                title: "Day 2",
                subtitle: "JUL 10"
            }]
        }
    }

}



const Agenda = () => {
    let userOffest = new Date().getTimezoneOffset() / -60
    const timeButtons = [{
        key: "venueTime",
        title: "Venue time",
        subtitle: "(UTC+2)"
    }, {
        key: "userTime",
        title: "Your time",
        subtitle: `(UTC${userOffest < 0 ? "" : "+"}${userOffest})`
    }]
    let timeSettings = "userTime";
    useEffect(() => {
        if (localStorage && localStorage.getItem && localStorage.getItem("userSelection")) {
            fnUpdateSelectedTime(localStorage.getItem("userSelection"), true);
        }
    }, [])

    let bUserTime = timeSettings === "userTime";
    const [selectedTime, setSelectedTime] = useState(timeSettings);

    const [dateButton, setDateButton] = useState(getDateButtonState(bUserTime));
    const [sessions, setSessions] = useState(fnGetSession(bUserTime, dateButton.selectedKey));
    const [selectedSession, setSelectedSession] = useState(null);

    const fnUpdateSelectedTime = (key, bResetDate) => {
        if (localStorage && localStorage.setItem) {
            localStorage.setItem("userSelection", key);
        }
        setSelectedTime(key);
        const newDateButtonState = getDateButtonState(key === "userTime", bResetDate ? undefined : dateButton.selectedKey)
        setDateButton(newDateButtonState)
        setSessions(fnGetSession(key === "userTime", newDateButtonState.selectedKey))
    }

    const fnUpdateSelectedDay = (key) => {
        setDateButton({
            ...dateButton,
            selectedKey: key
        });
        setSessions(fnGetSession(selectedTime === "userTime", key))
    }

    return (
        <Section id="agenda" cssClass={styleModule.Agenda}>
            <div className={styleModule.agenda__title}>
                <Title color="white" size="medium" text="Agenda" />
                <ToggleButtons buttons={timeButtons} selectedKey={selectedTime} onClick={fnUpdateSelectedTime} verticalAlign={true}/>
            </div>
            <div className={styleModule.agenda__dayToggle}>
                <ToggleButtons buttons={dateButton.buttons} selectedKey={dateButton.selectedKey} onClick={fnUpdateSelectedDay}/>
            </div>
            { sessions.map((session, index) => (
                <div
                    key={index}
                    className={cx(
                        styleModule.AgendaItem,
                        session.type === 'break' ? styleModule.view_break : null,
                    )}
                >
                    <div className={styleModule.agenda__startTime}>
                        {selectedTime === "venueTime" ? venueTimeFormatter.format(session.startTime) : userTimeFormatter.format(session.startTime)}
                    </div>
                    <div className={styleModule.AgendaAvatars}>
                        {
                            session.type === 'break' || session.type === 'pre_event'
                                ? (
                                    <SessionIcon type={session.type} />
                                )
                                : (
                                    session.speakers.map((speaker, speakerIndex) => (
                                        <Avatar
                                            onClick={() => setSelectedSession(session)}
                                            key={speakerIndex}
                                            src={speaker.photoUrl}
                                            alt={`${speaker.firstName} ${speaker.lastName}`}
                                        />
                                    ))
                                )
                        }
                    </div>
                    <div className={styleModule.agenda__desctiption}>
                        <AgendaSession session={session} full={false} onClick={session.type !== 'break' ? () => setSelectedSession(session) : null}/>
                    </div>
                </div>
            ))}
            { selectedSession
                ? (
                    <Dialog isOpen={true} onClose={() => setSelectedSession(null)}>
                        <AgendaSession session={selectedSession}/>
                    </Dialog>
                )
                : null
            }
        </Section>
    );
}



export default Agenda;