import PropTypes from "prop-types";
import cx from 'classnames';
import SpeakerCard from "../speakerCard";
import Biography from "../biography";

import styleModule from "./Agenda.module.css"

const rExpr = /^(.{150}[^\s]*).*/

const Session = ({session, full=true, onClick}) => (
    <div className={styleModule.session} >
        <div
            className={cx(
                styleModule.session__title,
                session.type !== 'break' ? styleModule.clickable : null,
                session.type === 'break' ? styleModule.view_break : null
            )}
        >
            <span
                className={styleModule.session__title_inner}
                onClick={onClick}
            >{session.title}</span>
        </div>
        {
            session.type !== "break"
                ? (
                    <>
                        <div className={styleModule.session__description}>
                            {!full && session.description.length > 200 ? `${session.description.replace(/(\r\n|\n|\r)/gm,"").replace(rExpr, "$1").replace(/[.,]*$/, '')}...` : session.description}
                        </div>
                        <div className={styleModule.session__speakers}>
                            {session.speakers.map((speaker, index) => full ? <Biography key={index} speaker={speaker} /> : <SpeakerCard key={index} speaker={speaker}/>)}
                        </div>
                    </>
                )
                : null
        }
    </div>
);


Session.propTypes = {
    session: PropTypes.object.isRequired,
    full: PropTypes.bool
}

export default Session;