import PropTypes from "prop-types";
import cx from "classnames"

import styles from "./Title.module.css"


const Title = ({text, size="default", color="default", hasMargin=false}) => (

    <div className={cx(styles.title, {[styles.has_margin]: hasMargin}, styles[`color_${color}`], styles[`size_${size}`])}>
        {text}
    </div>

);

Title.propTypes = {
    text: PropTypes.string.isRequired,
    size: PropTypes.oneOf(["default", "medium", "large"]),
    color: PropTypes.oneOf(["default", "light", "white"]),
    hasMargin: PropTypes.bool
}


export default Title;