import PropTypes from "prop-types";
import cx from "classnames"

import styleModule from "./SpeakerCard.module.css"


const SpeakerCard = ({speaker, showPhoto=false, isLargeSize=false}) => (
    <div className={cx(styleModule.speaker, isLargeSize ? styleModule.speaker_size_large: "" )} >
        {showPhoto && <img className={styleModule.speaker__photo} src={speaker.photoUrl}/>}
        <div className={styleModule.speaker__content}>
            <div className={styleModule.speaker__fullname}>{`${speaker.firstName} ${speaker.lastName}`}</div>
            <div className={styleModule.speaker__company}>{speaker.company}</div>
        </div>
    </div>
);


SpeakerCard.propTypes = {
    speaker: PropTypes.object.isRequired,
    showPhoto: PropTypes.bool,
    isLargeSize: PropTypes.bool
}

export default SpeakerCard;