import styles from "./Hero.module.css"
import { useRef } from 'react'
import { useSpring, useTrail, useChain, animated } from 'react-spring'
import HeaderBar from "../bar"
import Sharing from "../sharing"
import Title from "../title"
import { CalendarIcon, PinIcon } from "../icon";
import Logo from "../logo";
import IconText from "../iconText"
import Grid from "../grid"


const Hero = () => {

    const logoRef = useRef();
    const {opacity, top, filter} = useSpring({
        from: { opacity: 0, top: "calc(70% - 60px)", filter: "blur(20px)"},
        to: async (next, cancel) => {
            await next({opacity: 1, top: "calc(45% - 60px)"});
            await next({config: {duration: 1000}});
            await next({opacity: 0, top: "calc(25% - 60px)", filter: "blur(1px)", config: {duration: 300}});
        },
        ref: logoRef
    });

    const headerRef = useRef();
    const headerAnimation = useSpring({
        from: { opacity: 0, top: "30px" },
        to: {opacity: 1, top: "0"},
        config: {duration: 200},
        ref: headerRef
    });

    const content = [
        <Title color="light" text="Community conference around the UI5 framework" />,
        <Title color="light" size="large" hasMargin={true} text="Join us from home in July" />,
        <IconText icon={<PinIcon/>} text="Global online event" size="large" color="light"/>,
        <IconText icon={<CalendarIcon/>} text="July 9, 2020 / 16:00  20:30 CEST" size="large" color="light"/>,
        <IconText text="July 10, 2020 / 09:00  13:30 CEST" size="large" color="light"/>
    ];

    const contentRef = useRef();
    const trails = useTrail(content.length, {
        from: {opacity: 0, top: "30px"},
        to: {opacity: 1, top: "0px"},
        config: {duration: 400},
        ref: contentRef
    });

    const sharingRef = useRef();
    const sharingAnimation = useSpring({
        from: { opacity: 0, right: "0px" },
        to: {opacity: 1, right: "24px"},
        ref: sharingRef
    });

    useChain([logoRef, headerRef, contentRef, sharingRef]);

    const AnimatedHeader = animated(HeaderBar);
    const AnimatedGrid = animated(Grid);
    const AnimatedSharing = animated(Sharing)

    return (
        <div className={styles.hero}>
            <div className={styles.hero__background}>
                <AnimatedGrid style={{filter}}/>
            </div>
            <AnimatedHeader style={headerAnimation} />
            <AnimatedSharing title="Share" className={styles.hero__sharing} style={sharingAnimation}/>
            <div className={styles.hero__content}>
                {trails.map((animation, i) => (
                    <animated.div key={i} style={{...animation, position: "relative"}}>{content[i]}</animated.div>
                ))}
            </div>
            <animated.div className={styles.hero__logo} width="500px" height="158px" style={{opacity, top}}>
                <Logo animate={true} />
            </animated.div>
        </div>
    )
};


export default Hero;