import PropTypes from "prop-types";
import cx from "classnames"
import {useTransition, animated, config} from 'react-spring'

import styles from "./Sticker.module.css"


const Sticker = ({frontImageUrl, backImageUrl}) => {

    const transitions = useTransition(frontImageUrl, null, {
        initial: {transform: "translateX(0%) rotateY(0deg)"},
        from: { transform: "translateX(0%) rotateY(180deg)" },
        enter: { transform: "translateX(0%)  rotateY(0deg)" },
        leave: { transform: "translateX(0%) rotateY(-180deg)" },
        config: {
            duration: 1000
        }
    })

    return (
        <div className={styles.sticker}>
            <div className={styles.sticker__container}>
            {transitions.map(({ item, key, props }) =>
                <animated.img src={item} style={props} key={key}/>
            )}
            </div>
        </div>
    )
    // return (
    //     <div className={styles.sticker}>
    //         <div className={styles.sticker__container}>
    //             <div className={styles.sticker__front}>
    //                 {frontImageUrl && <img loading="lazy" src={frontImageUrl}/>}
    //             </div>
    //             <div className={styles.sticker__back}>
    //                 {<img loading="lazy" src="/hero/10.jpg"/>}
    //             </div>
    //         </div>
    //     </div>
    // )
};

Sticker.propTypes = {
    frontImageUrl: PropTypes.string,
    backImageUrl: PropTypes.string,
}


export default Sticker;