import Modal from 'react-modal';

import styleModule from "./Dialog.module.css"


Modal.setAppElement('body')


const Dialog = ({isOpen, children, onClose}) => {

    const fnHandleClose = () => {
        document.body.style.overflow="auto"
        onClose();
    }

    return (
        <Modal
            isOpen={isOpen}
            onAfterOpen={()=>document.body.style.overflow="hidden"}
            onRequestClose={fnHandleClose}
            className={styleModule.dialog}
            overlayClassName={styleModule.overlay}>
            {children}
            <div className={styleModule.dialog__close} onClick={fnHandleClose}></div>
        </Modal>
    );
}



export default Dialog;