import PropTypes from "prop-types";
import SpeakerCard from "../speakerCard";
import {TwitterIcon, LinkedinIcon} from "../icon";

import styleModule from "./Biography.module.css"



const Biography = ({speaker}) => (
    <div className={styleModule.biography} >
        <div className={styleModule.biography__title}>
            <div className={styleModule.biography__speaker}>
                <SpeakerCard speaker={speaker} showPhoto={true} isLargeSize={true}/>
            </div>
            <div className={styleModule.biography__network}>
                {speaker.twitter && speaker.twitter !== "null" ?
                    <a className={styleModule.biography__networkItem} target="_blank" href={speaker.twitter.startsWith("http") ? speaker.twitter : `https://twitter.com/${speaker.twitter}`}>
                        <TwitterIcon />
                    </a> : <></>
                }
                {speaker.linkedin && speaker.linkedin !== "null" ?
                    <a className={styleModule.biography__networkItem} target="_blank" href={speaker.linkedin.startsWith("http") ? speaker.linkedin : `https://www.linkedin.com/in/${speaker.linkedin}`}>
                        <LinkedinIcon />
                    </a> : <></>
                }
                {false&&speaker.github && speaker.github !== "null "?
                    <a className={styleModule.biography__networkItem} target="_blank" href={speaker.github.startsWith("http") ? speaker.github : `https://github.com/${speaker.github}`}>
                        <LinkedinIcon />
                    </a> : <></>
                }
            </div>
        </div>
        <div className={styleModule.biography__content}>
            {speaker.bio}
        </div>

    </div>
);



