import PropTypes from "prop-types";
import { useState, useEffect } from 'react'
import cx from "classnames"
import Sticker from "../sticker"

import styleModule from "./Grid.module.css"

const Grid = ({style={}}) => {

    const [grid, setGrid] = useState([0, 0])
    const [dispayedItems, setDisplayedItems] = useState([])
    const [cachedItems, setCachedItems] = useState(Array.from(Array(26), (_, i) => i + 1))

    //resize listener
    useEffect(() => {
        const queries = ['(max-width: 767px)', '(max-width: 1023px)' ];
        const relatedGrid = [[2, 3], [3, 4]];

        const mediaQueryLists = queries.map(q => window.matchMedia(q));
        const getValue = () => {
            const index = mediaQueryLists.findIndex(mql => mql.matches);
            return typeof relatedGrid[index] !== 'undefined' ? relatedGrid[index] : [4, 4];
        };
        const handler = () => setGrid(getValue())
        mediaQueryLists.forEach(mql => mql.addListener(handler));
        handler()

        return () => {
            mediaQueryLists.forEach(mql => mql.removeListener(handler))
        };
    }, []);

    //handle resize
    useEffect(() => {
        let gridSize = grid[0] * grid[1]
        if (dispayedItems.length > gridSize) {
            let removedItems = dispayedItems.splice(gridSize);
            setCachedItems(cachedItems.concat(removedItems))
            setDisplayedItems([...dispayedItems])
            setGrid(grid)
        } else if (dispayedItems.length < gridSize) {
            let addedItems = cachedItems.splice(0, gridSize - dispayedItems.length);
            setCachedItems([...cachedItems])
            setDisplayedItems(dispayedItems.concat(addedItems))
            setGrid(grid)
        }
    }, );

    //rotation
    useEffect(() => {
        const interval = setInterval(()=> {
            if (dispayedItems.length !== grid[0] * grid[1]) {
                return
            }
            let index = Math.floor(Math.random() * dispayedItems.length)
            let relacedItem = dispayedItems[index];
            dispayedItems[index] = cachedItems.splice(0, 1)[0];
            cachedItems.push(relacedItem);
            setDisplayedItems([...dispayedItems]);
            setCachedItems([...cachedItems]);
        }, 5000);
        return () => {
            clearInterval(interval);
        };
    }, );



    return (
        <div className={styleModule.grid} style={style}>
            {dispayedItems.map((item, index) =>
                <div key={index} className={cx(styleModule.grid__item, styleModule[`col-${grid[1]}`], styleModule[`row-${grid[0]}`])}>
                    <Sticker frontImageUrl={`./hero/${item}.jpg`} />
                </div>
            )}
        </div>
    );
}


Grid.propTypes = {
    style: PropTypes.object
}


export default Grid;